body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* base css */
.parent {
  background-color: rgb(75, 80, 83);
  padding: 1rem 
}
.child {
  text-align: center;
  white-space: nowrap;
  padding: 1rem 
}
.parent_bin_menu {
  text-align: center;
  white-space: nowrap;
  padding: 1rem 
}
.grid-parent_bin_menu {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 
}
.grid-parent {
  display: grid;
  grid-template-columns: calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) 
}
.parent_boxes_internal {
  background: #bbbaba;
  padding: 1rem 
}
.grid-parent_boxes_internal {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  background-color: rgb(164,172,176);
}
.parent_boxes {
  background: #808080;
  padding: 1rem 
}
.grid-parent_boxes {
  display: grid;
  grid-template-columns: 7% auto 7% 
}
